
.resume-render-wrapper {
  padding: 20px;
  position: fixed;
  left: 450px;
  right: 0;
  top: 58px;
  bottom: 0;
  overflow-x: hidden;
  overflow-y: auto;
}
.resume-render-wrapper * {
    font-size: 14px;
    padding: 0;
    margin: 0;
}
.resume-render-wrapper .resume-template {
    font-family: "Calibri", sans-serif;
    font-size: 12px;
    line-height: 1.5;
    background: #fff;
    padding: 40px;
}
.resume-render-wrapper .main img {
    height: 150px;
    width: 150px;
}
.resume-render-wrapper .text-right {
    text-align: right;
}
.resume-render-wrapper .inline-block {
    display: inline-block;
}
.resume-render-wrapper table {
    padding: 15px;
    width: 100%;
}
.resume-render-wrapper .page-break {
    page-break-after: always;
}
.resume-render-wrapper .w-10 {
    width: 10%;
}
.resume-render-wrapper .w-20 {
    width: 20%;
}
.resume-render-wrapper .w-30 {
    width: 30%;
}
.resume-render-wrapper .w-40 {
    width: 40%;
}
.resume-render-wrapper .w-50 {
    width: 50%;
}
.resume-render-wrapper .w-60 {
    width: 60%;
}
.resume-render-wrapper .w-70 {
    width: 70%;
}
.resume-render-wrapper .w-80 {
    width: 80%;
}
.resume-render-wrapper .w-90 {
    width: 90%;
}
.resume-render-wrapper .w-100 {
    width: 100%;
}
.resume-render-wrapper .intro {
    padding: 0 20px;
}
.resume-render-wrapper .intro h1 {
    font-size: 2rem;
    font-weight: bold;
    text-transform: uppercase;
    margin-bottom: 10px;
}
.resume-render-wrapper .intro p {
    display: block;
    margin-top: 15px;
    margin-bottom: 15px;
}
.resume-render-wrapper .section-header {
    border-bottom: solid 3px #666666;
    margin-bottom: 20px;
}
.resume-render-wrapper .section {
    margin-top: 20px;
}
.resume-render-wrapper .section th {
    text-align: left;
    padding: 10px;
}
.resume-render-wrapper .section td {
    padding: 10px;
}
.resume-render-wrapper .list li {
    margin-bottom: 10px;
}



