html, body {
  font-size: 12px;
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
  letter-spacing: 0.04em;
  background: #f0f3f7;
}
.hidden {
  display: none;
}
.icon-small {
  font-size: 1em;
}
.button {
  height: auto;
  padding: 6px 18px;
  border: none;
  border-radius: 2px;
  transition: all 0.3s ease-in-out;
  box-shadow: 0px 2px 3px rgba(0, 0, 0, 0.15);
}
.button:hover {
    box-shadow: 0px 3px 5px rgba(0, 0, 0, 0.15);
    transform: scale(1.02);
}
.button .icon {
    display: inline-block;
    margin-right: 0.5em !important;
}
#app {
  height: 100%;
}
.panel:not(:last-child) {
  margin-bottom: 0;
}
.panel .panel-header {
  background: #fff;
  padding: 15px;
  cursor: pointer;
  text-transform: uppercase;
  color: #7a7a7a;
  border-bottom: solid 1px #e8e8e8;
}
.panel .panel-header .icon {
    font-size: 0.8em;
    float: right;
}
.panel .panel-body {
  padding: 15px;
  background: #fff;
  border-bottom: solid 1px #e8e8e8;
}
.panel.open .panel-header {
  font-weight: bold;
}
.panel .panel {
  margin-bottom: 15px;
}
.panel .panel .panel-header {
    border: solid 1px #e8e8e8;
}
.panel .panel .panel-body {
    border: solid 1px #e8e8e8;
    border-top: none;
}
.navbar {
  background: #2998FF;
  text-align: center;
  position: fixed;
  top: 0;
  min-height: 58px;
  left: 0;
  right: 0;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.25);
  z-index: 10;
}
.navbar .generate-button {
    float: right;
    margin: 12px;
    color: #7a7a7a;
    padding-right: 30px;
    padding-left: 30px;
}
.navbar .generate-button:hover {
      color: #4a4a4a;
}
.navbar .logo {
    display: inline-block;
    padding: 20px;
}
.navbar .logo img {
      width: 20px;
      vertical-align: middle;
}
.navbar .account-tab {
    float: left;
    padding-top: 9px;
    color: white;
    font-size: 1.2em;
    text-transform: uppercase;
    padding: 18px;
    cursor: pointer;
    position: relative;
}
.navbar .account-tab .icon {
      font-size: 0.8em;
}
.navbar .account-tab img {
      width: 20px;
      height: 20px;
      display: inline-block;
      vertical-align: middle;
      border-radius: 50%;
      margin-right: 5px;
}
.navbar .account-tab .account-dropdown {
      position: absolute;
      top: 100%;
      left: 10%;
      margin-top: 1px;
      background: white;
      border-radius: 0 0 2px 2px;
      min-width: 200px;
      z-index: 12;
      box-shadow: 1px 2px 4px rgba(0, 0, 0, 0.15), -1px 0 4px rgba(0, 0, 0, 0.15);
}
.navbar .account-tab .account-dropdown span, .navbar .account-tab .account-dropdown a {
        font-size: 0.9em;
        display: block;
        padding: 10px 20px;
        border-bottom: solid 1px #dbdbdb;
        color: #2998FF;
}
.navbar .account-tab .account-dropdown span {
        cursor: default;
        color: #b5b5b5;
}
.sidebar-form label {
  font-weight: normal;
  text-transform: uppercase;
  color: #7a7a7a;
}
.sidebar-form .form-input {
  margin-bottom: 20px;
}
.sidebar-form .input {
  border: none;
  height: 3em;
  box-shadow: none;
  border-radius: 0;
  color: #4a4a4a;
  border-bottom: solid 2px #dbdbdb;
  transition: all 0.3s ease-in-out;
}
.sidebar-form .input:focus {
    color: #2998FF;
    border-bottom-color: #2998FF;
}
.sidebar-form .textarea {
  resize: none;
  min-height: 80px;
}
@keyframes fly {
0% {
    transform: translateY(0%) rotate3d(9, -9, 0, 30deg);
}
100% {
    transform: translateY(-5%) rotate3d(9, -9, 0, 30deg);
}
}
.splash {
  position: relative;
  height: 100%;
  background: #2998FF;
  background-image: linear-gradient(to left, #6A8FF7 40%, #2998FF 100%);
}
.splash .intro {
    position: relative;
    top: 50%;
    transform: translateY(-50%);
    max-width: 80%;
    margin: auto;
    width: 100%;
    z-index: 2;
}
.splash .logo {
    font-family: "Roboto Slab", serif;
    display: inline-block;
}
.splash .logo img {
      width: 40px;
      vertical-align: baseline;
}
.splash .logo span {
      color: white;
      font-size: 3em;
      vertical-align: baseline;
      margin-left: 5px;
}
.splash p {
    margin: 20px 0;
}
.splash .signin-button {
    border-radius: 2px;
    display: inline-block;
    font-size: 1.5em;
    padding: 12px 20px;
    color: #2998FF;
}
.splash .signin-button .icon {
      vertical-align: middle;
      margin-top: -2px;
      padding-right: 0px;
      min-width: 20px;
      margin-right: 0;
      color: #7a7a7a;
}
.splash .signin-button .button-label {
      border-left: solid 1px #dbdbdb;
      padding-left: 15px;
}
.splash .illustration {
    display: none;
    position: fixed;
    right: 10%;
    top: 50%;
    margin-top: -250px;
    animation-name: fly;
    animation-direction: alternate;
    animation-iteration-count: infinite;
    animation-play-state: running;
    animation-delay: 0;
    animation-duration: 2s;
    animation-timing-function: ease-in-out;
    z-index: 1;
}
@media only screen and (min-device-width: 900px) {
.splash .illustration {
    display: block;
}
}
.sidebar {
  background: #fff;
  position: fixed;
  left: 0;
  top: 58px;
  bottom: 0;
  max-width: 450px;
  width: 100%;
  color: #0057a9;
  overflow-y: auto;
  box-shadow: 5px 0 10px rgba(0, 0, 0, 0.05);
